.login__form {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #030c1c;
  max-width: 400px;
  border-radius: 10px;
  padding: 20px;
}
.login h1 {
  color: white;
  text-align: center;
}
.login .formulario {
  max-width: 700px;
  margin: auto;
}
.login .formulario label {
  color: white;
  padding: 10px 0px;
  display: block;
}
.login .formulario__input {
  transition: width 0.15s ease-in-out;
  margin: 5px auto;
  display: block;
  width: 95%;
  padding: 10px 20px 10px 40px;
  outline: none;
  border: none;
  border-radius: 4px;
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: 10px;
  font-size: 1.1rem;
}
.login .formulario__input:focus {
  width: 100%;
  box-shadow: 0px 0px 8px white;
}
.login .formulario__input[type=email] {
  background-image: url(../../static/media/email.05fa4801.svg);
}
.login .formulario__input[type=email]:focus {
  background-image: url(../../static/media/emailhover.e8b65a08.svg);
}
.login .formulario__input[type=password] {
  background-image: url(../../static/media/password.61dcee7f.svg);
}
.login .formulario__input[type=password]:focus {
  background-image: url(../../static/media/passwordhover.aab4b652.svg);
}
.login .formulario__submit {
  width: 95%;
